<template>
  <div>

<nav-bar :name="title"></nav-bar>

  <v-container class="text-center" v-if="!isStaff">
    <br>
    <v-menu
      bottom
      offset-y
      v-if="options"
    >
      <template v-slot:activator="{ on, attrs }">
      <v-btn
        dark
        v-bind="attrs"
        v-on="on"
        class="text-center"
        color="#E49711"
      > Payment options 
        <v-icon right size="26px"> mdi-arrow-down-drop-circle </v-icon>
      </v-btn>
      </template>
      <v-list>
        <v-list-item link>
          <v-list-item-title class="text-center">
            <router-link to="/makepayment"> Make a payment </router-link>
          </v-list-item-title>
        </v-list-item>

        <v-list-item link>
          <v-list-item-title class="text-center">
            <router-link to="/reportproblem"> Report a problem </router-link>
          </v-list-item-title>
        </v-list-item>

        <v-list-item link>
          <v-list-item-title class="text-center">
            <router-link to="/reportpayment"> Report a payment </router-link>
          </v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
    <br>
    <br>
  </v-container>

<v-container class="text-center">
      <v-card class="ma-auto" width="90%" outlined>
        <br>
        <v-card class="ma-auto" width="75%" tile>
          <p class="font-weight-bold text-left px-3 py-1">General Payments</p>
          <v-divider></v-divider>
            <v-expansion-panels focusable>
              <v-expansion-panel>
                <v-expansion-panel-header class="font-weight-light text-left px-2">
                  Payments
                </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    <v-card>
                    <v-card-title>
                      <v-text-field
                        v-model="search"
                        append-icon="mdi-magnify"
                        label="Search"
                        single-line
                        hide-details
                      ></v-text-field>
                    </v-card-title>
                    <v-data-table
                      :headers="headers"
                      :search="search"
                      :items="payments"
                      :loading="loading"
                      loading-text="Loading... Please wait"
                      hide-default-footer
                      :page.sync="page"
                      :items-per-page="itemsPerPage"
                      @page-count="pageCount = $event"
                      >
                <template v-slot:item.invoice="{ item }">
                  {{ item.invoice.store.name }}
                </template>
                <template v-slot:item.status="{ item }">
                  <v-chip
                    :color="getColor(item.status)"
                    dark
                  >
                    {{ item.status | name_status }}
                  </v-chip>
                </template>
                <template v-slot:item.method="{ item }">
                    {{ item.method | name_method }}
                </template>
                <template v-slot:item.unique_id="{ item }">
                  <v-menu
                    top
                    offset-x
                    v-if="select"
                  >
                    <template v-slot:activator="{ on, attrs }">
                    <v-btn icon
                      dark
                      v-bind="attrs"
                      v-on="on"
                    >
                        <v-icon right color="#E47911" size="26px" class="mb-2"> mdi-arrow-down-drop-circle </v-icon>
                    </v-btn>
                    </template>
                    <v-list>
                    <v-list-item link>
                      <v-list-item-title>
                        <router-link :to="{ name: 'ViewPayment', params: { 'id': item.unique_id }}"> Show payment details </router-link>
                      </v-list-item-title>
                    </v-list-item>
                    <v-list-item link>
                      <v-list-item-title>
                        <router-link :to="{ name: 'UpdatePayment', params: { 'id': item.unique_id }}"> Update payment </router-link>
                      </v-list-item-title>
                    </v-list-item>
                    </v-list>
                  </v-menu>
                    <v-checkbox
                      :value="item.unique_id"
                      color="#E47911"
                      v-model="idPayment"
                      v-else
                      class="my-n3"
                    ></v-checkbox>
                </template>
                    </v-data-table>
                  </v-card>
                    <v-pagination
                      v-model="page"
                      :length="pageCount"
                    ></v-pagination>

            <v-row v-if="!select">
              <v-col>
              <v-btn
              class="mx-4 my-2"
              color="#989898"
              dark
              @click="desactivarBtn"
              >
              CANCEL
              </v-btn>
              <v-btn
              class="mx-4 my-2"
              color="#E47911"
              dark
              >
              EXPORT
              </v-btn>
              </v-col>
              </v-row>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>

          <v-expansion-panels focusable>
              <v-expansion-panel>
                <v-expansion-panel-header class="font-weight-light text-left px-2">
                  Payments Reports
                </v-expansion-panel-header>
                  <v-expansion-panel-content>
                    <v-card>
                    <v-card-title>
                      <v-text-field
                        v-model="search"
                        append-icon="mdi-magnify"
                        label="Search"
                        single-line
                        hide-details
                      ></v-text-field>
                    </v-card-title>
                    <v-data-table
                      :headers="headers_reports"
                      :search="search_reports"
                      :items="payments_reports"
                      :loading="loading_reports"
                      loading-text="Loading... Please wait"
                      hide-default-footer
                      :page.sync="page_reports"
                      :items-per-page="itemsPerPage"
                      @page-count="pageCount_reports = $event"
                      >
                <template v-slot:item.payment="{ item }">
                  {{ item.payment.invoice.store.name }}
                </template>
                <template v-slot:item.status="{ item }">
                  <v-chip
                    :color="getColorReports(item.status)"
                    dark
                  >
                    {{ item.status | name_status_reports }}
                  </v-chip>
                </template>
                <template v-slot:item.unique_id="{ item }">
                  <v-menu
                    top
                    offset-x
                    v-if="select"
                  >
                    <template v-slot:activator="{ on, attrs }">
                    <v-btn icon
                      dark
                      v-bind="attrs"
                      v-on="on"
                    >
                        <v-icon right color="#E47911" size="26px" class="mb-2"> mdi-arrow-down-drop-circle </v-icon>
                    </v-btn>
                    </template>
                    <v-list>
                    <v-list-item link>
                      <v-list-item-title>
                        <router-link :to="{ name: 'ViewPayment', params: { 'id': item.unique_id }}"> Show payment details </router-link>
                      </v-list-item-title>
                    </v-list-item>
                    </v-list>
                  </v-menu>
                    <v-checkbox
                      :value="item.unique_id"
                      color="#E47911"
                      v-model="idPayment"
                      v-else
                      class="my-n3"
                    ></v-checkbox>
                </template>
                    </v-data-table>
                  </v-card>
                  <v-pagination
                    v-model="page_reports"
                    :length="pageCount_reports"
                  ></v-pagination>

              <v-row v-if="!select">
              <v-col>
              <v-btn
              class="mx-4 my-2"
              color="#989898"
              dark
              @click="desactivarBtn"
              >
              CANCEL
              </v-btn>
              <v-btn
              class="mx-4 my-2"
              color="#E47911"
              dark
              >
              EXPORT
              </v-btn>
              </v-col>
              </v-row>
              </v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>

              </v-card>
    <br>
    <p class="font-weight-bold text-center py-2">Total paid: $ {{ total }} </p>
      </v-card>
  </v-container>

<footer-nav></footer-nav>

  </div>
</template>

<script>
  import NavBar from '@/components/NavBar';
  import FooterNav from '@/components/Footer';
  import PaymentService from '@/services/PaymentService';
  import Swal from 'sweetalert2'

export default {
    components: {
      NavBar, FooterNav
    },

    data: () => ({
      title: 'PAYMENTS',
      select: true,
      options: true,
      loading: false,
      loading_reports: false,
      page: 1,
      pageCount: 0,
      page_reports: 1,
      pageCount_reports: 0,
      itemsPerPage: 5,
      select: true,
      export_btn_color: '#000',
      total: 0,
      types: [
        {
          name: "Payment"
        },
        {
          name: "Payment Reports"
        },
      ],
      payments: [ ],
      search: '',
      payments_reports: [ ],
      search_reports: '',
      idPayment: [ ],
      headers: [
        {
          text: 'Name store',
          value: 'invoice'
        },
        { 
          text: 'Amount',
          value: 'amount'
        },
        { 
          text: 'Status',
          value: 'status'
        },
        { 
          text: 'Date',
          value: 'date'
        },
        { 
          text: 'Method pay',
          value: 'method'
        },
        { 
          text: 'Actions',
          value: 'unique_id',
          sortable: false,
          search: false
        },
      ],
      headers_reports: [
        {
          text: 'Name store',
          value: 'payment'
        },
        { 
          text: 'Status',
          value: 'status'
        },
        { 
          text: 'Date',
          value: 'registered_at'
        },
        { 
          text: 'Description',
          value: 'issue_description'
        },
        { 
          text: 'Actions',
          value: 'unique_id',
          sortable: false,
          search: false
        },
      ],
    }),
    
    computed: {
      isStaff(){
        if (this.$store.state.auth.user.is_staff)
          return true;

        return false;
      },

      idStore(){
        if(!this.isStaff)
          return this.$store.state.auth.store_user.unique_id;
        
        return '';
      }
    },

    filters:{
      name_status(value){
        if (value == 'P') return 'Paid'
        else return 'Pending'
      },
      
      name_status_reports(value){
        if (value == 'Pd') return 'Pending'
        else if (value == 'R') return 'Resolved'
        else return 'In progress'
      },

      name_method(value){
        if (value == 'S') return 'Stripe'
        else if (value == 'T') return 'Transfer'
        else if (value == 'C') return 'Cash'
        else if (value == 'D') return 'Deposit'
        else return 'Other'
      },
    },
    
    methods: {
      activeExport(){
          this.select = !this.select;
          this.export_btn_color = this.select ? '#000' : '#E47911'
      },
      
      desactivarBtn(){
          this.select = !this.select;
          this.export_btn_color = '#000';
      },

      getColor (status) {
        if (status == 'P') return 'green'
        else if (status == 'Pd') return 'orange'
        else return 'red'
      },

      getColorReports (status) {
        if (status == 'R') return 'green'
        else if (status == 'Pd') return 'orange'
        else return 'red'
      },

      list_admin(){
        this.loading = true;
        PaymentService
        .list_admin()
        .then(res => {
          this.loading = false;
          this.payments = res.items;
          this.total = parseFloat(res.total).toFixed(2);
        })
        .catch(e => {
          let error = e.response.data;
          let msg = ''
          Object.entries(error).forEach(([ key, value ]) => {
            msg+= key + ': ' + value + ' \n ';
          });
            Swal.fire({
            icon: 'error',
            title: 'Error',
            text: msg
            });
        })
      },

      list_admin_reports(){
        this.loading_reports = true;
        PaymentService
        .list_admin_reports()
        .then(res => {
          this.loading_reports = false;
          this.payments_reports = res;
        })
        .catch(e => {
          let error = e.response.data;
          let msg = ''
          Object.entries(error).forEach(([ key, value ]) => {
            msg+= key + ': ' + value + ' \n ';
          });
            Swal.fire({
            icon: 'error',
            title: 'Error',
            text: msg
            });
        })
      },

      list_customer(){
        this.loading = true;
        PaymentService
        .list_customer(this.idStore)
        .then(res => {
          this.loading = false;
          this.payments = res.items;
          this.total = parseFloat(res.total).toFixed(2);
        })
        .catch(e => {
          let error = e.response.data;
          let msg = ''
          Object.entries(error).forEach(([ key, value ]) => {
            msg+= key + ': ' + value + ' \n ';
          });
            Swal.fire({
            icon: 'error',
            title: 'Error',
            text: msg
            });
        })
      },

      list_customer_reports(){
        this.loading_reports = true;
        PaymentService
        .list_customer_reports(this.idStore)
        .then(res => {
        this.loading_reports = false;
          this.payments_reports = res;
        })
        .catch(e => {
          let error = e.response.data;
          let msg = ''
          Object.entries(error).forEach(([ key, value ]) => {
            msg+= key + ': ' + value + ' \n ';
          });
            Swal.fire({
            icon: 'error',
            title: 'Error',
            text: msg
            });
        })
      },
  },

  mounted(){
    if(!this.isStaff){
      this.list_customer();
      this.list_customer_reports();
    }
    else{
      this.list_admin();
      this.list_admin_reports();
    }
  }

}
</script>