<template>
  <v-footer>
    <v-card
      flat
      tile
      width="100%"
      class="text-center"
    >
      <v-card-text v-if="isStaff">
        <router-link v-for="(item, id) in staff" :key="id" :to="item.to">
        <v-btn
          class="mx-2"
          icon
        >
          <v-icon size="23px">
            {{ item.icon }}
          </v-icon>
        </v-btn>
        </router-link>
      </v-card-text>

      <v-card-text v-else>
        <router-link to="/sales">
        <v-btn
          class="mx-2"
          icon
        >
          <v-icon size="23px">
            mdi-currency-usd
          </v-icon>
        </v-btn>
        </router-link>
        <router-link :to="{ name: 'ViewStore', params: {'id':data_store.unique_id}}">
        <v-btn
          class="mx-2"
          icon
        >
          <v-icon size="23px">
            mdi-shopping
          </v-icon>
        </v-btn>
        </router-link>
        <router-link v-for="(item, id) in items" :key="id" :to="item.to">
        <v-btn
          class="mx-2"
          icon
        >
          <v-icon size="23px">
            {{ item.icon }}
          </v-icon>
        </v-btn>
        </router-link>
      </v-card-text>
    </v-card>
  </v-footer>
</template>

<script>
  export default {
    name: 'FooterNav',

    data: () => ({
      staff: [
        {
          icon: "mdi-currency-usd",
          to: "/sales"
        },
        {
          icon: "mdi-shopping",
          to: "/stores"
        },
        {
          icon: "mdi-account-supervisor",
          to: "/users"
        },
        {
          icon: "mdi-credit-card-outline",
          to: "/payments"
        },
        {
          icon: "mdi-account-group",
          to: "/referrals"
        },
        {
          icon: "mdi-file-document-outline",
          to: "/invoices"
        },
      ],
      items: [
        {
          icon: "mdi-credit-card-outline",
          to: "/payments"
        },
        {
          icon: "mdi-account-group",
          to: "/referrals"
        },
        {
          icon: "mdi-file-document-outline",
          to: "/invoices"
        },
      ],
    }),

    computed: {
    /* SELECCIONA LA PRIMERA TIENDA DEL USUARIO */
      data_store(){
        return this.$store.state.auth.store_user;
      },
    
    isStaff(){
      if (this.$store.state.auth.user.is_staff)
        return true;

      return false;
    },
    },
  }
</script>